<template>
  <div>
    <p>{{ msg }}</p>
  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    name: 'Ping',
    data() {
      return {
        msg: '',
      };
    },
    methods: {
      getMessage() {
        const path = 'https://wusel.space/api/ping';
        axios.get(path)
          .then((res) => {
            this.msg = res.data;
          })
          .catch((error) => {
            // eslint-disable-next-line
            console.error(error);
          });
      },
    },
    created() {
      this.getMessage();
    },
  };
</script>
